@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Open+Sans:wght@300&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html, body {
    margin: 0;
    padding: 0;
    text-align: none !important;
}

a {
    text-decoration: none;
}

.row {
    margin: 0;
}

.logo {
    font-family: 'Lobster', cursive;
    font-size: 36px;
    color: black !important;    
}

.custom-nav {    
    position: absolute;
    width: 100%;
    z-index: 1;    
}

.links {
    text-align: right;
    color: rgb(0, 0, 0) !important;
    font-family: sans-serif;
    font-weight: 600;
}

.navbar-toggler {
    color:rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.0);
    border-color: rgba(255, 255, 255, 0.0) !important;
}

span.navbar-toggler-icon {    
    color:rgb(0, 0, 0);
}

.navbar-dark .navbar-toggler-icon {    
    transition: 0.8s;
    background-image: url(data:image/png;base64,);
}

.navbar-toggler:not(.collapsed) .navbar-toggler-icon {    
    margin: 0 auto;
    transition: 0.8s;
    width: 30px;
    height: 30px;      
    background-image: url(data:image/png;base64,);
}

button:focus:not(:focus-visible) {
    display: none;
}

button.navbar-toggler{
    padding-right: 0;
}

/* media-screen */
@media screen and (max-width: 560px){    
    .logo {
        font-size: 24px;
    } 
    .navbar-toggler {
        font-size: 16px;
    }
    
    .links {
        font-size: 12px;
    }
}

html, body {
    margin: 0;
    padding: 0;
    text-align: none !important;
}

.HeroImage {
    padding: 0;
}

img {      
    width: 100%;
    position: relative;    
}

.confetti {
    background: rgba(24, 24, 24, 0.0);
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
}

.city {
    border-radius: 0;    
}

.submit-button {
    background: rgb(0, 0, 0, 0.5);
    border-radius: 0;
    border: none;        
}

.submit-button:hover {
    background: rgba(75, 74, 74, 0.5);
}

.submit-col-1 {
    padding-left: 0;
}

.form-col-6 {
    padding-right: 0px;
    border: none;
}

.search-bar {
    position: relative;
    bottom: 200px;
}

.form-control {
    height: 36px;
}

@media screen and (max-width: 768px){
    .search-bar {        
        bottom: 150px;
    } 
    
    .submit-button {
        font-size: 14px;    
    }

    .form-control {
        height: 32px;
    }
}

@media screen and (max-width: 576px){
    .submit-button {
        font-size: 12px;    
    }

    .form-control {
        font-size: 12px;
    }

    .form-control {
        height: 30px;
    }

    .search-bar {
        position: relative;
        bottom: 100px;
    }        
}

@media screen and (max-width: 400px){
    .submit-button {
        font-size: 8px;
        margin-bottom: 4px;   
    }

    .form-control {
        height: 24px;
    }

    .search-bar {
        position: relative;
        bottom: 100px;
    }        
}


.carousel {
    margin-top: 50px !important;  
    width: 220px;
    margin: 0 auto;
    box-shadow: 5px 5px 5px 5px rgb(187, 186, 186); 
    padding-bottom: 20px;  
}

.sales {
    margin-top: 50px !important;    
    width: 100%;
    height: 300px;    
    background-image: url(https://i.gifer.com/2ii9.gif);    
    border-radius: 10px;
    z-index: 999;        
}

.sales-overlay {
    position: relative;
    width: 100%;
    height: 300px; 
    background: rgba(255, 0, 0);
    bottom: 70px;
    left: 0;
    z-index: -1;
}

p {
    font-family: 'Lobster', cursive;
    font-size: 40px;
    position: relative;
    top: 40%;
}

.offer-header {
    font-family: 'Lobster', cursive;
    border-bottom: 2px solid black;    
    margin: 0;
    margin: 0 20px 0 20px;     
}

.carousel-caption h3 {
    font-size: 20px;
    background: rgba(0,0,0,0.5);
    border-radius: 2px;
    color: #D4AF37;
}

.carousel-control-next-icon, .carousel-control-prev-icon {
    display: none;
}
.carousel-indicators {
    background-color: rgba(0, 0, 0, 0.5) !important;
}

/* media-screen */
@media screen and (max-width: 992px){
    .carousel-caption h3 {
        font-size: 20px;
    }    
    
}

@media screen and (max-width: 751px){
    .sales {
        margin: 0 auto;
        width: 80%;
        height: 150px;        
    } 
    
    .sales-overlay {
        margin: 0 auto;
        width: 100%;
        height: 150px; 
    }

    p {       
        top: 30%;
        font-size: 30px;
    }
}

@media screen and (max-width: 560px){
    .carousel-caption h3 {
        font-size: 16px;
    } 
    
    .carousel {
        width: 150px;
    }    
    
    p {       
        font-size: 24px;
    }    
}


.Departments img{
    width: 220px;
    margin: 20px;
    box-shadow: 5px 5px 5px 5px rgb(187, 186, 186);
    position: relative;
    z-index: 999;  
}

.Departments img:hover { /* Safari 3-8 */
    transform: scale(0.98);
    opacity: 0.8; 
  }

.category-header {
    font-family: 'Lobster', cursive;
    margin-top: 20px !important;
    border-bottom: 2px solid black;
    margin: 0 20px 0 20px;  
}

@media screen and (max-width: 560px){
    .Departments img{
        width: 150px;        
    }       
}

